'use strict';

if (!OCA.Inventory) {
		OCA.Inventory = {};
}
$(document).ready(function () {

	var Items = { template: '<div>items</div>' };
	var Places = { template: '<div>places</div>' };
	var Categories = { template: '<div>categories</div>' };

	var routes = [
	// using
	// { path: '/items', component: Items, alias: '/' },
	// instead of
	{ path: '/', redirect: '/items' }, { path: '/items', component: Items },
	// would also be an option, but it currently does not work
	// reliably with router-link due to
	// https://github.com/vuejs/vue-router/issues/419
	{ path: '/places', component: Places }, { path: '/categories', component: Categories }];

	var router = new VueRouter({
		routes: routes // short for `routes: routes`
	});

	OCA.Inventory.App = new Vue({
		el: '#app',
		router: router,
		data: {
			active: 'items',
			views: [{
				name: t('inventory', 'Items'),
				id: "items"
			}, {
				name: t('inventory', 'Places'),
				id: "places"
			}, {
				name: t('inventory', 'Categories'),
				id: "categories"
			}]
		}
	});
});